<template>
<div class ="login">
<h1>Login</h1>
<input type="text" v-model="name" placeholder="enter user name" />
<input type="password" v-model="password" placeholder="enter password" />
<button v-on:click="login" >Login </button>
<p> <router-link to="/sign-up"> Sign Up </router-link> </p>
</div>
</template>
<script>
import axios from 'axios'
export default {
    name:'Login-page',
    data()
    {
        return {
            email:'',
            password:''
        }

    },
    methods:{
        async login()
        {
            console.warn('calling login method',this.name,this.password)   
            let result = await axios.get(`https://jsonserverapi-3mto.onrender.com/users?name=${this.name}&password=${this.password}`);
            console.warn(result); 
            console.warn(result.data.length);
            if(result.status == 200 && result.data.length > 0 )
            {               
                window.localStorage.setItem("user-info", JSON.stringify(result.data));
                this.$router.push({name:'Home'})
            } 
            else
            {
                 this.$toast.error('Not a valid user.')
            }      
        }
    }
}
</script>