<template>
<Header />
<h1>Hello {{name}}, welcome on Home Screen </h1>

<table border="1" >
<tr>
<td>Id</td>
<td>Name</td>
<td>Contact</td>
<td>Address</td>
<td>Action</td>
</tr>
<tr v-for="item in restaurant" :key="item.id">

<td>{{item.id}}</td>
<td>{{item.name}}</td>
<td>{{item.contact}}</td>
<td>{{item.address}}</td>
<td><router-link :to="'/update/'+item.id"> Update </router-link>
<button class="btnDelete" v-on:click="deleteRestaurant(item.id)"> Delete </button>
 </td>
</tr>
</table>
</template>
<script>
import Header from './Header.vue'
import axios from 'axios'
export default {
    name:'Home-page',
    components:{
        Header
    },
    data()
    {
        return {
            name:'',
            restaurant:[]
        }

    },
    methods:{
         async deleteRestaurant(id)
        {
            let result = await axios.delete("https://jsonserverapi-3mto.onrender.com/restaurants/"+id)
            if(result.status == 200)
            {
                this.loadData()
            }
        },
        async loadData()
        {
            let user = window.localStorage.getItem("user-info");
            this.name = JSON.parse(user)[0].name;
            if(!user)
            {
                this.$router.push({name:'login'})
            }

            let result = await axios.get("https://jsonserverapi-3mto.onrender.com/restaurants")
            this.restaurant = result.data
        }
    },
    async mounted()
    {        
       this.loadData()
    }
}
</script>
<style>
table{
    margin-left:450px;
}
td
{
    width:160px;
    height:40px
}

.btnDelete{
    margin-left:20px
}
</style>