<template>
<Header />
<form class="add">
<h1>Add Restaurant </h1>
<input type="text"  v-model="restaurant.name" name="name" placeholder="enter name" />
<input type="text"  v-model="restaurant.contact" name="contact" placeholder="enter contact no" />
<input type="text"  v-model="restaurant.address" name="address" placeholder="enter address" />
<button type="button" v-on:click="addRestaurant" >Add New Restaurant </button>
</form>
</template>
<script>
import Header from './Header.vue'
import axios from 'axios'
export default {
    name:'Add-page',
    components:{
        Header
    },
    data()
    {
        return {
            restaurant : {
                name:'',
                contact:'',
                addres:''
            }
        }

    },
    mounted()
    {        
        let user = window.localStorage.getItem("user-info");
        if(!user)
        {
            this.$router.push({name:'login'})
        }
    },
    methods:{
        async addRestaurant()
        {
            console.warn('calling add Restaurant method',this.restaurant)
            let result = await axios.post("https://jsonserverapi-3mto.onrender.com/restaurants",{
                name:this.restaurant.name,
                contact:this.restaurant.contact,
                address:this.restaurant.address
                });

            console.warn("result",result)   
            
            if(result.status == 201)
            {
                this.$toast.success('Restaurant added successfully.')
                this.$router.push({name:'Home'})
            }
            else
            {
                 this.$toast.error('Some issue while adding restaurant.')
            }  
        }
    }
}
</script>